export default function ViewData(props) {
  var classes = useStyles();
  const { ...rest } = props;

  const location = useLocation();
  const counter = useSelector((state) => state.counter);
  const dispatch = useDispatch();

  const [scaleSelector, setScaleSelector] = React.useState("");
  const [tHeight, settHeight] = React.useState(false);
  const [openOverlay, setOpenOverlay] = React.useState(true);
  var [resError, setResError] = React.useState("");
  const [ErrorSnack, setopenErrorSnack] = React.useState(false);
  const [radarChartData, setRadarChartData] = React.useState({});
  const [barChartData, setBarChartData] = React.useState({});
  const [lineChartData, setLineChartData] = React.useState({});
  const [scalesChoose, setScalesChoose] = React.useState([]);
  var [currnetScale, setCurrnetScale] = React.useState("");
  const [subScaleParagraphs, setSubScaleParagraphs] = React.useState([]);
  const [afterProgressParagraphs, setAfterProgressParagraphs] = React.useState(
    {}
  );

  const handleSelectorChange = (event) => {
    // setCitygErr("");
    let currentLableTemp = "";
    setScaleSelector(event.target.value);
    counter.dataResults.labelsAndScales.forEach((element) => {
      if (element.scale === event.target.value) {
        currentLableTemp = element.scale;
        setCurrnetScale(element.label);
      }
    });
    console.log(currentLableTemp);
    //__Bar graph__
    let barDataTemp = {};
    barDataTemp.labels = [
      "Average",
      "Self",
      "Superiors",
      "Subordinates",
      "Peers",
      "Aggregation",
    ];
    barDataTemp.datasets = [];
    barDataTemp.datasets[0] = {};
    barDataTemp.datasets[0] = {
      label: event.target.value, //sa
      backgroundColor: "rgba(255,99,132,0.2)",
      borderColor: "rgba(255,99,132,1)",
      borderWidth: 1,
      hoverBackgroundColor: "rgba(255,99,132,0.4)",
      hoverBorderColor: "rgba(255,99,132,1)",
    };
    barDataTemp.datasets[0].data = [];

    barDataTemp.labels.forEach((element, index) => {
      barDataTemp.datasets[0].data[index] =
        counter.dataResults.dataForEachDimenstion[currentLableTemp][element];
    });
    setBarChartData(barDataTemp);

    //__Progress Line Chart__
    let lineDataTemp = {};
    lineDataTemp.labels = [];
    lineDataTemp.labels =
      counter.dataResults.progressDataForEachDimenstion.dates;
    lineDataTemp.datasets = [];
    lineDataTemp.datasets[0] = {};
    lineDataTemp.datasets[0] = {
      label: event.target.value,
      fill: false,
      lineTension: 0.1,
      backgroundColor: "rgba(75,192,192,0.4)",
      borderColor: "rgba(75,192,192,1)",
      borderCapStyle: "butt",
      borderDash: [],
      borderDashOffset: 0.0,
      borderJoinStyle: "miter",
      pointBorderColor: "rgba(75,192,192,1)",
      pointBackgroundColor: "#fff",
      pointBorderWidth: 1,
      pointHoverRadius: 5,
      pointHoverBackgroundColor: "rgba(75,192,192,1)",
      pointHoverBorderColor: "rgba(220,220,220,1)",
      pointHoverBorderWidth: 2,
      pointRadius: 7,
      pointHitRadius: 10,
    };
    lineDataTemp.datasets[0].data = [];
    lineDataTemp.datasets[0].data =
      counter.dataResults.progressDataForEachDimenstion[currentLableTemp];

    setLineChartData(lineDataTemp);

    //__subScale Paragraphs__
    let subScalePragraphsTemp = [];
    let paragraphsNames = [
      "AggregateParagraph",
      "SelfParagraph",
      "SuperiorParagraph",
      "SubordinateParagraph",
      "PeerParagraph",
      "CoachingParagraph ",
    ];
    paragraphsNames.forEach((element, index) => {
      subScalePragraphsTemp[index] =
        counter.dataResults.paragraphsForEachDimenstion[currentLableTemp][
          element
        ];
    });
    setSubScaleParagraphs(subScalePragraphsTemp);
  };

  const handleErrorSnackClose = () => {
    setopenErrorSnack(false);
    // setopenSuccessSnack(false);
  };

  useEffect(() => {
    document.title = "Data Processing";
    dispatch(dispatchTest("ddd"));
    console.log("counter: " + counter.test);
    // console.log("sss: " + location.pathname.split("/")[2]);
    if (location.pathname.split("/")[2])
      HttpClient.getResults(
        //   Access("userInfo")["id"],
        location.pathname.split("/")[2]
      )
        .then((response) => {
          // console.log("asdada/ //__ " + response);
          response = JSON.parse(response);
          if (response.response_code === 0) {
            dispatch(dispatchResults(response.data));
            //__Radar graph__
            let radarTemp = {};
            radarTemp.labels = [];
            radarTemp.datasets = [];
            radarTemp.datasets[0] = {};
            radarTemp.datasets[1] = {};
            radarTemp.datasets[0] = {
              label: "Responded Rating",
              backgroundColor: "rgba(179,181,198,0.2)",
              borderColor: "rgba(179,181,198,1)",
              pointBackgroundColor: "rgba(179,181,198,1)",
              pointBorderColor: "#fff",
              pointHoverBackgroundColor: "#fff",
              pointHoverBorderColor: "rgba(179,181,198,1)",
            };
            radarTemp.datasets[1] = {
              label: "Aggregation",
              backgroundColor: "rgba(255,99,132,0.2)",
              borderColor: "rgba(255,99,132,1)",
              pointBackgroundColor: "rgba(255,99,132,1)",
              pointBorderColor: "#fff",
              pointHoverBackgroundColor: "#fff",
              pointHoverBorderColor: "rgba(255,99,132,1)",
            };

            radarTemp.datasets[0].data = [];
            radarTemp.datasets[1].data = [];

            response.data.radarChartData.forEach((element, index) => {
              radarTemp.labels[index] =
                response.data.radarChartData[index].label;
              radarTemp.datasets[0].data[index] =
                response.data.radarChartData[index].avg_rating;
              radarTemp.datasets[1].data[index] =
                response.data.radarChartData[index].aggregation;
            });
            setRadarChartData(radarTemp);

            //__Current Scale__
            let labelsTemp = [];
            let ScaleTemp = [];
            let currentLableTemp = "";

            currentLableTemp = response.data.labelsAndScales[0].label;
            setCurrnetScale(response.data.labelsAndScales[0].label);

            response.data.labelsAndScales.forEach((element, index) => {
              labelsTemp[index] = element.label;
              ScaleTemp[index] = element.scale;
            });
            setScalesChoose(labelsTemp);
            setScaleSelector(labelsTemp[0]);

            //__Bar graph__
            let barDataTemp = {};
            barDataTemp.labels = [
              "Average",
              "Self",
              "Superiors",
              "Subordinates",
              "Peers",
              "Aggregation",
            ];
            barDataTemp.datasets = [];
            barDataTemp.datasets[0] = {};
            barDataTemp.datasets[0] = {
              label: currentLableTemp,
              backgroundColor: "rgba(255,99,132,0.2)",
              borderColor: "rgba(255,99,132,1)",
              borderWidth: 1,
              hoverBackgroundColor: "rgba(255,99,132,0.4)",
              hoverBorderColor: "rgba(255,99,132,1)",
            };
            barDataTemp.datasets[0].data = [];

            barDataTemp.labels.forEach((element, index) => {
              barDataTemp.datasets[0].data[index] =
                response.data.dataForEachDimenstion[ScaleTemp[0]][element];
            });
            setBarChartData(barDataTemp);

            //__Progress Line Chart__
            let lineDataTemp = {};
            lineDataTemp.labels = [];
            lineDataTemp.labels =
              response.data.progressDataForEachDimenstion.dates;
            lineDataTemp.datasets = [];
            lineDataTemp.datasets[0] = {};
            lineDataTemp.datasets[0] = {
              label: currentLableTemp,
              fill: false,
              lineTension: 0.1,
              backgroundColor: "rgba(75,192,192,0.4)",
              borderColor: "rgba(75,192,192,1)",
              borderCapStyle: "butt",
              borderDash: [],
              borderDashOffset: 0.0,
              borderJoinStyle: "miter",
              pointBorderColor: "rgba(75,192,192,1)",
              pointBackgroundColor: "#fff",
              pointBorderWidth: 1,
              pointHoverRadius: 5,
              pointHoverBackgroundColor: "rgba(75,192,192,1)",
              pointHoverBorderColor: "rgba(220,220,220,1)",
              pointHoverBorderWidth: 2,
              pointRadius: 7,
              pointHitRadius: 10,
            };
            lineDataTemp.datasets[0].data = [];
            lineDataTemp.datasets[0].data =
              response.data.progressDataForEachDimenstion[ScaleTemp[0]];

            setLineChartData(lineDataTemp);

            //__subScale Paragraphs__
            let subScalePragraphsTemp = [];
            let paragraphsNames = [
              "AggregateParagraph",
              "SelfParagraph",
              "SuperiorParagraph",
              "SubordinateParagraph",
              "PeerParagraph",
              "CoachingParagraph",
            ];
            paragraphsNames.forEach((element, index) => {
              subScalePragraphsTemp[index] =
                response.data.paragraphsForEachDimenstion[ScaleTemp[0]][
                  element
                ];
            });
            setSubScaleParagraphs(subScalePragraphsTemp);

            //__After Proress Paragraphs__
            let strength_avg_weak_responseTemp = {};
            let strength_avg_weak_responseTempLables = [];
            strength_avg_weak_responseTempLables = [
              "Average Standing",
              "Strengths",
              "Weaknesses",
            ];
            strength_avg_weak_responseTemp["Average Standing"] = [];
            strength_avg_weak_responseTemp.Strengths = [];
            strength_avg_weak_responseTemp.Weaknesses = [];

            strength_avg_weak_responseTempLables.forEach((element) => {
              strength_avg_weak_responseTemp[element] =
                response.data.strength_avg_weak_response[element];
            });
            setAfterProgressParagraphs(strength_avg_weak_responseTemp);
          } //counter.dataResults
          setOpenOverlay(false);
        })
        .catch((error) => {
          console.log("ERROR :" + error);
          setOpenOverlay(false);
          setResError("Network Error");
          setopenErrorSnack(true);
        });
  }, []);

  return (
    <div style={{ height: "100%", overflowY: "hidden" }}>
      <Header
        color="transparent"
        // routes={dashboardRoutes}
        brand={<div>Data Processing </div>}
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 400,
          color: "white",
        }}
        {...rest}
      />
      <div style={{ width: "100%", display: "flex", justifyContent: "center" }}>
        <img height="600" src={team2} className={classes.imgClass} />
      </div>

      <div className={classes.firstContainer}>
        <div className={classes["containerIf"]}>
          <div className={classes["iframeUpDiv"]}>
            <p style={{ fontSize: "20px" }}>How to Interpret your findings</p>
            <iframe
              className={classes[".responsive-iframe"]}
              // width={"800"}
              // height="400"
              src="https://www.youtube.com/embed/2ZF4OM6mrrI"
            ></iframe>
          </div>
        </div>
        <div className={classes.radarDiv}>
          {/* <h2>Bar Example (custom size)</h2> */}
          <Radar data={radarChartData} />

          <div
            style={{
              marginTop: "40px",
              display: "flex",
              justifyContent: "center",
              width: "100%",
              textAlign: "center",
            }}
          >
            <FormControl className={classes.formControl}>
              <InputLabel id="demo-mutiple-name-label">
                choose a subScale
              </InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                className={classes.selectWidth}
                // error={CitySelectorErrorToggel}
                value={scaleSelector}
                onChange={handleSelectorChange}
              >
                {scalesChoose.map((name) => (
                  <MenuItem key={name} value={name}>
                    {name}
                  </MenuItem>
                ))}
              </Select>
              <Button
                // href="https://www.creative-tim.com/product/material-kit-react?ref=mkr-navbar"
                // color=""
                target="_blank"
                // className={classes.navLink}
                style={{
                  color: "#fff",
                  textTransform: "none",
                  background: "#FF006E",
                  width: "100px",
                  height: "40px",
                  marginLeft: "60px",
                  // textTransform: "none",
                  fontWeight: "500",
                  fontSize: "14px",
                  borderRadius: "4px",
                }}
                onClick={() => {
                  if (tHeight === false) settHeight(true);
                  else settHeight(false);

                  console.log("we");
                }}
              >
                Show Chart
                {/* <CloudDownload style={{color: "#000"}} className={classes.icons} />  */}
              </Button>
            </FormControl>
          </div>
        </div>
      </div>
      <Collapse isOpen={tHeight}>
        <div
          style={{
            width: "100%",
            height: "auto",
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Avatar
            className={classes.Avatar}
            style={{ width: "250px", height: "250px" }}
            alt={"Test"}
            src={imgChoose(scaleSelector)}
          />

          <div className={classes.barStyle}>
            <Bar
              // styel={{width: "50%"}}
              data={barChartData}
              // width={100}
              // height={400}
              options={{
                maintainAspectRatio: false,
              }}
              className={classes.barTest}
            />
            {/* <h2>Radar Example</h2> */}
          </div>
        </div>
        <ProductSection
          currnetScaleName={currnetScale}
          data={lineChartData}
          paragraphs={subScaleParagraphs}
        />
      </Collapse>
      <TeamSection paragraphs={afterProgressParagraphs} />
      <Footer />
      <Snackbar
        open={ErrorSnack}
        autoHideDuration={6000}
        onClose={setopenErrorSnack}
      >
        <Alert onClose={handleErrorSnackClose} severity="error">
          {resError}
        </Alert>
      </Snackbar>

      <Backdrop
        className={classes.backdrop}
        open={openOverlay}
        onClick={() => {
          setOpenOverlay(false);
        }}
        onClose={() => {
          setOpenOverlay(false);
        }}
      >
        <SpinnerLoader />
      </Backdrop>
    </div>
  );
}
