import team2 from "assets/img/Monitoring.png";
import team3 from "assets/img/Reward_reliance.png";
import team4 from "assets/img/Punishment_Or.png";
import team5 from "assets/img/Only_Pres_for_Probs.png";
import team6 from "assets/img/Rules_Lawyer.png";
import team7 from "assets/img/Ideal_Inf.png";
import team8 from "assets/img/Int_Stim.png";
import team9 from "assets/img/Indiv_Con.png";
import team10 from "assets/img/Mot.png";
import team11 from "assets/img/Democratic_Inc_Lead.png";
// import team12 from "assets/img/Servant_Lead.png";
import team13 from "assets/img/Neg_Management.png";
import team14 from "assets/img/Pos_Leadership.png";

export const imgChoose = (scale) => {
  if (scale === "Symp_Neg_Lead") return team7;
  if (scale === "Monitoring") return team2;
  if (scale === "Reward_reliance") return team3;
  if (scale === "Punishment_Or") return team4;
  if (scale === "Only_Pres_for_Probs") return team5;
  if (scale === "Rules_Lawyer") return team6;
  if (scale === "Ideal_Inf") return team7;
  if (scale === "Int_Stim") return team8;
  if (scale === "Indiv_Con") return team9;
  if (scale === "Mot") return team10;
  if (scale === "Democratic_Inc_Lead") return team11;
  if (scale === "Servant_Lead") return team5;
  if (scale === "Neg_Management") return team13;
  if (scale === "Pos_Leadership") return team14;
  else return team2;
};
