export function updateFiles(files, files2, files3, req) {
  var formData = new FormData();
  // console.log(uploadedImage);
  files.map((file) => {
    formData.append("csv", file);
  });
  if (files2.length !== 0)
    files2.map((file) => {
      formData.append("textSheet", file);
    });
  else formData.append("textSheetDate", req.textSheet);
  if (files3.length !== 0)
    files3.map((file) => {
      formData.append("varSheet", file);
    });
  else formData.append("varSheetDate", req.varSheet);

  const config = {
    headers: {
      Accept: "application/json",
      "Content-Type": "multipart/form-data",
      charsets: "utf-8",
    },
  };
  return axios
    .post("/UploadSheets", formData, config)
    .then((result) => {
      return new Promise(function (resolve, reject) {
        console.log("inside feedBack " + result.data);
        resolve(JSON.stringify(result.data));
      });
    })
    .catch((error) => {
      console.log("ERROR :" + error);
    });
}

