import React from "react";
// import './App.css';
import lottie from "lottie-web";
import animationData from "../../../assets/img/4903-failed(1).json";

let animObj = null;

class LottieTest extends React.Component {
  componentDidMount() {
    console.log("componentDidMount");

    //call the loadAnimation to start the animation
    animObj = lottie.loadAnimation({
      container: this.animBox, // the dom element that will contain the animation
      renderer: "svg",
      loop: true,
      autoplay: true,
      animationData: animationData, // the path to the animation json
    });
  }
  handleStop = () => {
    animObj.stop();
  };
  handlePlay() {
    animObj.play();
  }
  render() {
    return (
      <div className="App">
        {/* <div
          style={{
            width: '300px',
            height: '150px',
            // display: 'flex',
            // justifyContent: 'center',
            // alignItems: 'center',
            // flexDirection: 'column',
          }}
          className="wrap-login100"> */}
        {/* rgba(29, 210, 177, 0.7) rgb(58, 39, 207)*/}
        <span
          style={{
            marginBottom: "30px",
            marginTop: "20px",
            color: "rgba(243, 240, 255, 1)",
          }}
          className="login100-form-title p-b-37"
        >
          Wrong Credentials{" "}
        </span>
        {/* <h2 style={{color: '#fff'}}></h2> */}
        {/* This is you wrapper where animation will load */}
        <div
          style={{ width: 100, margin: "0 auto" }}
          ref={(ref) => (this.animBox = ref)}
        />
        {/* <button onClick={this.handleStop}>Stop</button>
        <button onClick={this.handlePlay}>Play</button> */}
      </div>
      //   </div>
    );
  }
}

export default LottieTest;
