import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Backdrop from "@material-ui/core/Backdrop";
import People from "@material-ui/icons/People";
import SpinnerContent from "../../components/SpinnerLoader/SpinnerLoader";
import LottieTest from "../Components/lottie/lottieTest";
// core components
// import Header from "components/Header/Header.js";
// import HeaderLinks from "components/Header/HeaderLinks.js";
// import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import SimpleReactValidator from "simple-react-validator";
import styles from "assets/jss/material-kit-react/views/loginPage.js";

import image from "assets/img/bg7.jpg";

const useStyles = makeStyles(styles);

class SpinnerLoader extends React.Component {
  constructor(props) {
    super(props);
    console.log("cred: " + this.props.WrongCredentails);
    // state = {};
  }

  render() {
    if (this.props.WrongCredentails == true) return <LottieTest />;
    else return <SpinnerContent color={"#fff"} />;
  }
}

export default function LoginPage(props) {
  let history = useHistory();

  const [openOverlay, setOpenOverlay] = React.useState(false);
  const [confirmed, setConfirmed] = React.useState(false);
  const validator = React.useRef(new SimpleReactValidator());
  const [UserName, setUserName] = React.useState("");
  const [ErrUserName, setErrUserName] = React.useState("");
  const [Passwd, setPasswd] = React.useState("");
  const [ErrPasswd, setErrPasswd] = React.useState("");

  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  setTimeout(function () {
    setCardAnimation("");
  }, 700);
  const classes = useStyles();
  const { ...rest } = props;

  useEffect(() => {
    document.title = "Login";
  }, []);

  return (
    <div>
      <div
        className={classes.pageHeader}
        style={{
          backgroundImage: "url(" + image + ")",
          backgroundSize: "cover",
          backgroundPosition: "top center",
        }}
      >
        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem xs={12} sm={12} md={4}>
              <Card className={classes[cardAnimaton]}>
                <form className={classes.form}>
                  <CardHeader color="primary" className={classes.cardHeader}>
                    <h4>Login</h4>
                  </CardHeader>
                  <p className={classes.divider}>Welcome</p>
                  <CardBody>
                    <CustomInput
                      labelText="User Name..."
                      id="first"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        type: "text",
                        endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        ),
                        onChange: (event) => {
                          setErrUserName("");
                          setUserName(event.target.value);
                          // console.log("TTT");
                        },
                      }}
                    />
                    <p style={{ color: "red" }}>{ErrUserName}</p>
                    {/* <CustomInput
                      labelText="Email..."
                      id="email"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        type: "email",
                        endAdornment: (
                          <InputAdornment position="end">
                            <Email className={classes.inputIconsColor} />
                          </InputAdornment>
                        ),
                      }}
                    /> */}
                    <CustomInput
                      labelText="Password"
                      id="pass"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        type: "password",
                        endAdornment: (
                          <InputAdornment position="end">
                            <Icon className={classes.inputIconsColor}>
                              lock_outline
                            </Icon>
                          </InputAdornment>
                        ),
                        autoComplete: "off",
                        onChange: (event) => {
                          setErrPasswd("");
                          setPasswd(event.target.value);
                          // console.log("TTT");
                        },
                      }}
                    />
                    <p style={{ color: "red" }}>{ErrPasswd}</p>
                  </CardBody>
                  <CardFooter className={classes.cardFooter}>
                    <Button
                      onClick={() => {
                        if (UserName !== "" || Passwd !== "") {
                          setConfirmed(false);
                          setOpenOverlay(true);
                          setTimeout(() => {
                            if (UserName !== "Aaron" || Passwd !== "123456")
                              setConfirmed(true);
                            else
                              history.push({
                                pathname: "/adminUploadCSV",
                              });
                          }, 3000);
                        } else {
                          if (
                            UserName === "" ||
                            validator.current.fieldValid("UserName") === false
                          ) {
                            console.log("inside1");
                            setErrUserName("Please Enter User name");
                          }
                          if (
                            Passwd === "" ||
                            validator.current.fieldValid("Passwd") === false
                          ) {
                            console.log("inside2");
                            // setPasswd("Enter Passwd")
                            setErrPasswd("Please Enter Password");
                          }
                        }
                      }}
                      simple
                      color="primary"
                      size="lg"
                    >
                      Get started
                    </Button>
                  </CardFooter>
                </form>
              </Card>
            </GridItem>
          </GridContainer>
        </div>
      </div>
      <Backdrop
        className={classes.backdrop}
        open={openOverlay}
        onClick={() => {
          setOpenOverlay(false);
        }}
        onClose={() => {
          setOpenOverlay(false);
          setConfirmed(false);
        }}
      >
        <SpinnerLoader WrongCredentails={confirmed} />
      </Backdrop>
    </div>
  );
}
