export const LOGIN_SUCCESS_CLIENTS = "LOGIN_SUCCESS_CLIENTS";
export const LOGIN_SUCCESS_ORDERS = "LOGIN_SUCCESS_ORDERS";
export const USER_INFO = "USER_INFO";
export const GET_RESULTS = "USER_INFO";
export const TEST = "TEST";

export const dispatchTest = (value) => {
  return {
    type: TEST,
    payload: value,
  };
};

export const dispatchResults = (value) => {
  return {
    type: GET_RESULTS,
    payload: value,
  };
};

export const dispatchOrdersList = (value) => {
  return {
    type: LOGIN_SUCCESS_ORDERS,
    payload: value,
  };
};

export const dispatchUserInfo = (value) => {
  return {
    type: USER_INFO,
    payload: value,
  };
};

