import { GET_RESULTS, TEST } from "../actions/ActionsList";

export const MyState = {
  SerivceLists: [],
  test: "gg",
  dataResults: {},
};

export const counter = (state = MyState, action) => {
  switch (action.type) {
    case TEST:
      return {
        ...state,
        test: action.payload,
      };
    case GET_RESULTS:
      return {
        ...state,
        dataResults: action.payload,
      };
    default:
      return state;
  }
};
